'use client'
import React, { useEffect, useRef, forwardRef } from 'react'
import styles from '@/styles/progress/progress.module.css'
import Image from 'next/image'
import gsap from 'gsap'

const Progress = forwardRef<HTMLDivElement, { activeIndex: number }>(
  ({ activeIndex }, ref) => {
    const progressBoxRef = useRef<HTMLDivElement | null>(null)

    // 연도, 텍스트, 이미지 데이터
    const years = ['2015.4-2016.8', '2016.8-2017.3', '2017.3-2018.10']
    const titles = [
      '帝都渋谷ビル',
      '金谷ホテルマンション',
      'エグゼクティブタワー',
    ]
    const subtitles = ['間借り5坪', '間借り10坪', '間借り30坪']
    const images = [
      '/images/section/progressSection/progress01.png',
      '/images/section/progressSection/progress02.png',
      '/images/section/progressSection/progress03.png',
    ]

    useEffect(() => {
      if (!progressBoxRef.current) return

      const yearText = progressBoxRef.current.querySelector('.progressYear p')
      const buildingText = progressBoxRef.current.querySelector(
        '.progressBuilding p:first-child'
      )
      const subtitleText = progressBoxRef.current.querySelector(
        '.progressBuilding p:last-child'
      )
      const image = progressBoxRef.current.querySelector('.imgarea img')

      if (yearText && buildingText && subtitleText && image) {
        // 연도 변경 애니메이션
        gsap.to(yearText, {
          opacity: 0,
          y: -20,
          duration: 0.3,
          onComplete: () => {
            yearText.textContent = years[activeIndex]
            gsap.to(yearText, { opacity: 1, y: 0, duration: 0.3 })
          },
        })

        // 건물 이름 변경 애니메이션
        gsap.to(buildingText, {
          opacity: 0,
          y: -20,
          duration: 0.3,
          onComplete: () => {
            buildingText.textContent = titles[activeIndex]
            gsap.to(buildingText, { opacity: 1, y: 0, duration: 0.3 })
          },
        })

        // 서브텍스트 변경 애니메이션
        gsap.to(subtitleText, {
          opacity: 0,
          y: -20,
          duration: 0.3,
          onComplete: () => {
            subtitleText.textContent = subtitles[activeIndex]
            gsap.to(subtitleText, { opacity: 1, y: 0, duration: 0.3 })
          },
        })

        // 이미지 변경 애니메이션
        gsap.to(image, {
          opacity: 0,
          scale: 0.95,
          duration: 0.3,
          onComplete: () => {
            image.setAttribute('src', images[activeIndex])
            gsap.to(image, { opacity: 1, scale: 1, duration: 0.3 })
          },
        })
      }
    }, [activeIndex])

    return (
      <section className="sec progress" ref={ref}>
        <section className="secInner">
          <div className="titleArea">
            <Image
              src="/images/section/PROGRESS.svg"
              width={693}
              height={158}
              alt="PROGRESS"
            />
            <h2 className="secTitle">これまでの軌跡</h2>
          </div>
          <section className={styles.progressArea}>
            <article
              className={`progressBox ${styles.progressBox}`}
              ref={progressBoxRef}
            >
              <div className={styles.textarea}>
                <div className={styles.progressYear}>
                  <p className="bunyan-bold">{years[0]}</p>
                </div>
                <div className={styles.progressBuilding}>
                  <p>{titles[0]}</p>
                  <p>{subtitles[0]}</p>
                </div>
              </div>
              <div className={styles.progressbar}></div>
              <div className={styles.imgarea}>
                <figure>
                  <Image
                    src={images[0]}
                    width={296}
                    height={368}
                    alt={titles[0]}
                  />
                </figure>
                <div className={styles.imgareaText}>
                  <span className="bunyan-regular">{titles[0]}</span>
                  <span></span>
                  <span className="bunyan-regular">01</span>
                </div>
              </div>
            </article>
          </section>
        </section>
      </section>
    )
  }
)

export default Progress
